/**
 * [MockGeturl	]
 * @param  {string} Api [Apiurl]
 * @return {[type]}     []
 */
export const RegularApi = (Api) => {
	return new RegExp(Api);
}

/**
 * Mock
 * @param  {object} option 
 * @return {object}        urltpyebody
 */
export const SplitOption = (option) => {
	let rs;
	if(option.type == "POST") {
		rs = option.body;
	} else {
		rs = option.url.split("?")[1];
	}
	if(!rs) {
		return option;
	}
	let arr = rs.split("&");
	let json = {};
	for(let i=0; i < arr.length; i++) {
		if(arr[i].indexOf("=")!=-1) {
			json[arr[i].split("=")[0]] = arr[i].split("=")[1];
		} else {
			json[arr[i]] = "undefined";
		}
	}
	option.url = option.url.split("?")[0];
	option.body = json;
	return option;
}


