import * as Api from './ApiInterface'
import { GetQueryString, GetInfo } from './DealFun.js'
// 
export const GET_ALL_STUDENT_TABLE = {
	url: Api.GET_ALL_STUDENT,
	cols: [[
		{type: 'numbers', width: '60', title: '', fixed: 'left'},
		{field: 'studentNumber', width: '135', title: ''},
		{field: 'studentName', width: '150', title: ''},
		{field: 'sex', width:100, title: '', FunDeal: {
			Fun: 'ReplaceState',
			StateNum: '1|0',
			StateText: '|'
		}},
		{field: 'studentIdcard', width: '180', title: ''},
		{fixed: 'right', width:'80', align:'center', toolbarList: [{
			title: '',
			style: 'layui-btn-normal',
			event: 'edit'
		}]}
	]],
};

