import Bus from './Bus.js';
import * as EmitType from './EmitType.js';

// 
export const ShowToast = msg => {
	layer.msg(msg)
}

// 
export const ShowLoading = () => {
	Bus.$emit(EmitType.ShowLoading)
}

// 
export const CloseLoading = () => {
	Bus.$emit(EmitType.CloseLoading)
}


