<template>
	<div class="OpenWindow-box">
		<el-form :inline="true" ref="formform" :model="OpenInfo" label-width="100px">
			<el-form-item label="" prop="teacherNumber">
				<el-input v-model="OpenInfo.teacherNumber" :disabled="true" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="teacherName">
				<el-input v-model="OpenInfo.teacherName" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="teacherIdcard">
				<el-input v-model="OpenInfo.teacherIdcard" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="" prop="sex">
				<el-select v-model="OpenInfo.sex" placeholder="">
					<el-option
						v-for="item in SexList"
						:key="item.value"
						:label="item.name"
						:value="item.value">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="" prop="teacherPassword">
				<el-input v-model="OpenInfo.teacherPassword" placeholder="" type="password"></el-input>
			</el-form-item>
			<el-form-item label="" prop="teacherPassworded">
				<el-input v-model="teacherPassworded" placeholder="" type="password"></el-input>
			</el-form-item>
			<el-form-item label="" prop="age">
				<el-input v-model="OpenInfo.age" placeholder=""></el-input>
			</el-form-item>
		</el-form>
		<el-button type="primary" size="small" class="add-btn" @click="add"></el-button>
	</div>
</template>

<script>
	import moment from 'moment/moment'
	export default {
		name: 'OpenWindow',
		data() {
			return {
				passworded: '',
				SexList: [{ name: '', value: 0 }, { name: '', value: 1 }],
				teacherPassworded: ''
			}
		},
		methods: {
			add() {
				const { teacherId, teacherNumber, teacherName, teacherPassword, teacherIdcard, sex, age } = this.OpenInfo;
				if(age === '' || teacherNumber === '' || teacherName === '' || teacherPassword === '' || teacherIdcard === '' || sex === '') {
					this.$ShowToast('');
					return;
				}
				if(teacherPassword != this.teacherPassworded) {
					this.$ShowToast('');
					return;
				}
				this.$Post(this.$Api.CHANGE_TEACHER_INFO, {
					teacherId, teacherNumber, teacherName, teacherPassword, teacherIdcard, sex, age
				}).then(res => {
					if(res.code == 0) {
						this.$ShowToast('');
						this.$emit('refresh');
						this.$emit('update:open', false);
					} else {
						this.$ShowToast(res.msg);
					}
				})
			}
		},
		computed: {},
		props: {
			OpenInfo: [Object],
			open: Boolean
		},
		watch: {
			open() {
				this.teacherPassworded = this.OpenInfo.teacherPassword
			}
		},
	}
</script>

<style lang="stylus" scoped>
	.OpenWindow-box >>> .el-form-item__content
		width: 205px;
	.OpenWindow-box >>> .el-date-editor.el-input,
	.OpenWindow-box >>> .el-date-editor.el-input__inner
		width: 205px !important;
	.OpenWindow-box
		width: 660px;
		height: 310px;
		background: #fff;
		padding-top: 15px;
	.add-btn
		margin-right: 35px;
		margin-top: -10px;
		float: right;
</style>


